
import Vue from 'vue'
import App from './App'
import VueRouter from 'vue-router'
import router from './router/router'

Vue.use(VueRouter);
import axios from './axios/index'
Vue.prototype.$axios = axios

import InlineCalendar from 'vux/src/components/inline-calendar/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

Vue.component('inline-calendar', InlineCalendar)
Vue.component('x-input', XInput)
Vue.component('datetime', Datetime)
Vue.component('x-textarea', XTextarea)
Vue.component('x-button', XButton)



















import { queryList } from "./api/index"
import util from "./util/util"
router.beforeEach((to, from, next) => {
let code = util.getCode('code')
alert(code)
if (!code) {
util.weixinauth()

} else {
queryList({ code: code }).then(res => {
console.log(res, 36)
alert(res.data.openid + '' + res.data.nickname)
if( res == 1 ) {
localStorage.setItem('Authorization', res.data.token)
}
})

next()
}
})

/* eslint-disable no-new */

new Vue({
el: '#app',
router,
components: { App },
template: '<App/>'
})



